import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Image from "next/image";

interface ProductCardProps {
  id: string;
  name: string;
  price: number;
  imageUrl: string;
  description: string;
}

export function ProductCard({ name, price, imageUrl, description }: ProductCardProps) {
  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('fr-FR').format(price);
  };

  return (
    <Card className="overflow-hidden">
      <CardContent className="p-0">
        <div className="relative aspect-[4/5]">
          <Image
            src={imageUrl}
            alt={name}
            fill
            priority
            className="object-cover transition-transform duration-300 hover:scale-105"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          />
        </div>
        <div className="p-4">
          <h3 className="font-semibold text-lg mb-2">{name}</h3>
          <p className="text-sm text-muted-foreground mb-2">{description}</p>
          <p className="text-lg font-bold">{formatPrice(price)} GNF</p>
        </div>
      </CardContent>
      <CardFooter className="p-4 pt-0">
        <Button 
          className="w-full"
          onClick={() => {
            const message = `Bonjour, je suis intéressé(e) par ${name} à ${formatPrice(price)} GNF`;
            window.open(`https://wa.me/611820064?text=${encodeURIComponent(message)}`, '_blank');
          }}
        >
          Contacter sur WhatsApp
        </Button>
      </CardFooter>
    </Card>
  );
}
